    import h5Copy from '@/common/h5Copy.js'
	export default {
        created() {
            this.getData()
            uni.getSystemInfo({
                success(res) {
                  this.listHeight=res.windowHeight-300;
                }
            })
        },
		data() {
			return {
				team:[],
                listHeight:540,
                URL:getApp().globalData.URL,
			};
		},
        methods:{
            async getData(){
                await this.$u.get('get_team').then(res=>this.team=res.data)
                },
                copyCode(){

                    uni.setClipboardData({
                        data: 'http://www.taskarea.top/pages/register/register?INC='+this.team.code,
                        success: function () {
                            uni.showToast({
                                title:"复制邀请链接成功!",
                                icon:"none"
                            })
                        }
                    }); 














                  
                }
        }
	}
